import { useSdk } from '@cetus/sdk-factory'
import { useAccountStore } from '@cetus/stores'
import envConfigs from '@cetus/types/src/config/envConfigs'
import { useSignAndExecuteTransaction, useSignPersonalMessage, useSignTransaction } from '@mysten/dapp-kit'
import { SuiTransactionBlockResponse } from '@mysten/sui/client'
import { Transaction } from '@mysten/sui/transactions'
import { fromBase64 } from '@mysten/sui/utils'
import { SignedTransaction } from '@mysten/wallet-standard'
import { useMemo } from 'react'

export function useSigner() {
  const clmmSdk = useSdk('clmm')
  const { currentAccount } = useAccountStore()
  const { mutateAsync: signTransaction } = useSignTransaction()
  const { mutate: signPersonalMessage } = useSignPersonalMessage()
  const { mutate: signAndExecuteTransaction } = useSignAndExecuteTransaction({
    execute: async ({ bytes, signature }) => await executeTransactionBlock(bytes, signature)
  })

  const chain = useMemo(() => {
    return envConfigs.env === 'mainnet' ? 'sui:mainnet' : 'sui:testnet'
  }, [currentAccount])

  /**
   * ÊâßË°å‰∫§Êòì
   * @param sdk
   * @param rawBytes
   * @param signature
   * @returns
   */
  const executeTransactionBlock = async (
    rawBytes: string,
    signature: string | string[],
    useDevInspect = false
  ): Promise<SuiTransactionBlockResponse> => {
    if (useDevInspect) {
      const res = await clmmSdk!.FullClient.devInspectTransactionBlock({
        transactionBlock: Transaction.from(fromBase64(rawBytes)),
        sender: currentAccount!.address
      })

      console.log('devInspectTransactionBlock:', res)

      if (!res || res.effects?.status.status === 'failure') {
        throw Error(res.effects?.status.error)
      }
    }

    const result = await clmmSdk!.FullClient.executeTransactionBlock({
      transactionBlock: rawBytes,
      signature,
      options: {
        showEvents: true,
        showRawEffects: true,
        showEffects: true,
        showBalanceChanges: true,
        showObjectChanges: true,
        showInput: true
      }
    })

    return result
  }

  /**
   * Âè™Á≠æÂêçÔºå‰∏çÂèëÈÄÅ‰∫§Êòì
   * @param transactionBlock
   * @returns
   */

  const signTransactionBlock = async (transactionBlock: Uint8Array | Transaction | string): Promise<SignedTransaction> => {
    console.log('üöÄüöÄüöÄ ~ file: useSigner.ts:74 ~ signTransactionBlock ~ transactionBlock:', transactionBlock instanceof Uint8Array, transactionBlock)
    if (transactionBlock instanceof Uint8Array) {
      transactionBlock = Transaction.from(transactionBlock)
    }

    if (typeof transactionBlock === 'string') {
      transactionBlock = Transaction.from(fromBase64(transactionBlock))
    }
    console.log('üöÄ ~ file: useSigner.ts:105 ~ signTransactionBlock ~ transactionBlock:', {
      transactionBlock,
      chain
    })

    return new Promise((resolve, reject) => {
      signTransaction(
        {
          transaction: transactionBlock as Transaction,
          chain
        },
        {
          onSuccess: result => {
            console.log('signTransactionBlock result', result)
            resolve(result)
          },
          onError: error => {
            console.error('signTransactionBlock failed', error)
            reject(error)
          },
          onSettled: (data: any, error: any, variables: any, context: any) => {
            // console.error('signTransactionBlock onSettled', {
            //   data,
            //   error,
            //   variables
            // })
          }
        }
      )
    })
  }

  /**
   *
   * @param tx
   * @returns
   */
  const signAndExecuteTransactionBlock = async (transactionBlock: Transaction, useDevInspect = false): Promise<SuiTransactionBlockResponse> => {
    let tx: Transaction

    if (typeof transactionBlock === 'string') {
      tx = Transaction.from(transactionBlock)
    } else {
      tx = transactionBlock
    }
    console.log('chain: ', {
      chain,
      transactionBlock
    })

    if (useDevInspect) {
      const res = await clmmSdk!.FullClient.devInspectTransactionBlock({
        transactionBlock,
        sender: currentAccount!.address
      })

      console.log('devInspectTransactionBlock:', res)

      if (!res || res.effects?.status.status === 'failure') {
        throw Error(res.effects?.status.error)
      }
    }
    // TODO ÊµãËØïcetus pumpÊó∂  Âú®‰∏ªÁΩë ËøÅÁßªÁöÑtx ÈúÄË¶ÅËÆæÁΩÆsetGasBudget Èí±ÂåÖÊâçËÉΩÈ¢ÑËÆ°ÁÆóÈÄöËøáÔºå‰∏¥Êó∂ÂÜôÊ≠ª‰∏Ä‰∏™ÂÄºÔºü
    // tx.setGasBudget(100000000)
    console.log('üöÄ ~ file: useSigner.ts:71 ~ signTransactionBlock ~ transactionBlock:', transactionBlock, chain)
    return new Promise((resolve, reject) => {
      signAndExecuteTransaction(
        {
          transaction: tx,
          chain
        },
        {
          onSuccess: result => {
            console.log(' signAndExecuteTransactionBlock  executed transaction', result)
            resolve(result)
          },
          onError: error => {
            console.error('signAndExecuteTransactionBlock transaction failed', error)
            reject(error)
          }
        }
      )
    })
  }

  // notifiÁ≠æÂêç‰ΩøÁî®
  const signUserMessage = async (message: Uint8Array): Promise<any> => {
    console.log('üöÄüöÄüöÄ ~ useSigner.ts:167 ~ signUserMessage ~ message:', message)
    console.log('üöÄüöÄüöÄ ~ useSigner.ts:167 ~ returnnewPromise ~ currentAccount:', currentAccount)
    return new Promise((resolve, reject) => {
      signPersonalMessage(
        {
          message
        },
        {
          onSuccess: result => {
            console.log('signUserMessage result', result)
            resolve(result)
          },
          onError: error => {
            console.error('signUserMessage failed', error)
            reject(error)
          },
          onSettled: (data: any, error: any, variables: any, context: any) => {
            // console.error('signUserMessage onSettled', {
            //   data,
            //   error,
            //   variables
            // })
          }
        }
      )
    })
  }

  return {
    signAndExecuteTransactionBlock,
    signTransactionBlock,
    executeTransactionBlock,
    signUserMessage
  }
}
